

html {
  font-size: 13px;
  background-color: #000000c3;
  color: #c7c5c5;
  height: 100vh;
  width: 97%;
}

.content {
  /* display: grid;
  grid-auto-rows: 1fr 1fr 1fr;
  grid-template-columns: 1fr; */
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin: 10px;
  margin-bottom: 0;
  height: auto;
  width: 100%;
}

.content > * {
  box-sizing: border-box;
}

h1 {
  margin-top: 1px; 
}
h3 {
  margin-top: 0;
}

button, select, input {
  outline: none;
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 5px;
  color: white;
  border: 2px solid rgb(48, 48, 48);
  background-color: #464646;
}

button:hover, #delete:hover, select:hover, input:hover {
  background-color: #5c5c5c;
  border: 2px solid rgb(20, 20, 20);
}

input:focus {
  background-color: #626262;
  border: 2px solid rgb(179, 179, 179);
}

@media screen and (min-width:1200px) {
  .content {
    flex-direction: initial;
    margin: 20px;
  }
}

.simulation {
  position: fixed;
  top: 5%;
  right: 13%;
}

.simulation #simulation {
  width: 90px;
}

.loosing {
  color: rgb(202, 33, 33);
}

.weapon {
  color: rgb(253, 181, 74);
}

.shield {
  color: rgb(37, 183, 250);
}

span {
  transition: 2s ease-in-out;
  font-size: x-large;
  font-weight: 500;
}

.win {
  color: rgb(38, 224, 38);
  animation-name: win;
  animation-duration: 12s;
  animation-delay: 3s;
}

.death {
  color: rgb(238, 31, 31);
  animation-name: death;
  animation-duration: 12s;
  animation-delay: 3s;
}

@keyframes win {
  from {
    color: rgb(38, 224, 38);
  }

  to {
    color: #cbcbcb;
  }
}

@keyframes death {
  from {
    color: rgb(238, 31, 31);
  }

  to {
    color: #cbcbcb;
  }
}